import { useState } from "react";
import { Link } from "react-router-dom";

export default function UserSideview({ datas, name }) {
  return (
    <>
      {datas && datas.name ? (
        <span className="sv_wrap">
          <Link
            to="#"
            className="sv_member"
            title={datas.name + " "}
            target="_blank"
            rel="nofollow"
          >
            <span className="profile_img">
              <img src={datas.absolute_user_icon_url} alt="" />
            </span>{" "}
            {datas.name}
          </Link>
          <span className="sv">
            {datas.memo_url && <Link to={datas.memo_url}></Link>}
            {datas.profile_url && <Link to={datas.profile_url}></Link>}
            {datas.user_posts_url && (
              <Link to={datas.user_posts_url}></Link>
            )}
          </span>
        </span>
      ) : (
        <span
          className="bo_guest"
          dangerouslySetInnerHTML={{ __html: name }}
        ></span>
      )}
    </>
  );
}
